import { Component, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ConsultantService } from '../../encounter/services/consultant.service';
import { DepartmentService } from '../../master/department/services/department.service';
import { Consultant } from '../../encounter/masters/consultant';
import { Encounter } from '../../encounter/masters/encounter';
import { EncounterService } from '../../encounter/services/encounter.service';
import { Department } from '../../master/department/masters/department';
import { LookupEntity } from '../../master/lookup-module/masters/lookup-entity';
import { LookupService } from '../../master/lookup-module/services/lookup.service';
import { Patient } from '../../patient/master/patient';
import { patientDocument } from './masters/patient-document';
import { PatientDocumentsService } from './services/patient-documents.service';
import { routerTransition } from '../../router.animations';

@Component({
	selector: 'app-patient-document',
	templateUrl: './patient-document.component.html',
	styleUrls: ['./patient-document.component.css'],
	animations: [routerTransition()]
})
export class PatientDocumentComponent implements OnInit {
	encounterList: Array<Encounter>;
	encounter: Encounter = new Encounter();
	@Output() PatientList;
	@Output() patient: Patient = new Patient();
	patientDocuments: patientDocument = new patientDocument();
	fileName: string;
	encounterId: number;
	File_name: string;
	totalFiles: number = 0;
	myFiles: string[] = [];
	fileNames: string[] = [];
	public documentList: any[] = [];
	files: any[] = [];
	selectedFiles?: FileList;
	currentFile?: File;
	progress = 0;
	documentPath: string;
	departmentList: Array<Department> = new Array<Department>();
	department: Department = new Department();
	consultantList: Array<Consultant> = new Array<Consultant>();
	lookupEntityList: Array<LookupEntity> = new Array<LookupEntity>();
	file: File;
	patientDForm = new FormGroup({});
	documentUploadedDate: Date = new Date();
	@Input() isPatientDocumentsWithPatientControl: Boolean = true;
	mainhead: string = "";
	mainHeadLink: string = "";
	category: LookupEntity = new LookupEntity();
	catDemo = this.category;
	constructor(private encounterService: EncounterService,
		private patientDocumentsService: PatientDocumentsService,
		private lookupService: LookupService, private fb: FormBuilder,
		private departmentService: DepartmentService,
		private consultationService: ConsultantService,
		private router: Router) { }

	ngOnInit(): void {
		this.setMainHead();
		this.reloadData();
		this.reloadForm();
		/*this.fileInfos =  this.patientDocumentsService.getFiles();*/
	}
	setMainHead() {
		let url = this.router.url;
		let urlList = url.split("/")
		if (urlList[1] == "nurseWorkbench") {
			this.mainhead = "Nurse Work Bench";
			this.mainHeadLink = "/nurseWorkbench"
		}
		else if (urlList[1] == "doctorsWorkbench") {
			this.mainhead = "Doctors Work Bench";
			this.mainHeadLink = "/doctorsWorkbench"
		}
		if (localStorage.getItem('encounter') != null) {
			this.encounter = JSON.parse(localStorage.getItem('encounter'));
			this.encounterId = this.encounter.encounterId;
		}

	}
	reloadData() {
		this.encounterService.getEncounterById(Number(this.encounterId)).subscribe(data => {
			this.encounter = data;
			this.patient = this.encounter.appointment.patient;
			/*console.log(this.patient)*/
		})

		this.departmentService.getDepartmentList().subscribe(data => {
			this.departmentList = data;
		})

		this.consultationService.getConsultantList().subscribe(data => {
			this.consultantList = data;
		})
		this.lookupService.getLookupEntityList().subscribe(data => {
			this.lookupEntityList = data;
		})


	}

	reloadForm() {
		this.patientDForm = this.fb.group({
			category: new FormControl(null),
			subCategory: new FormControl(null),
			fileDescription: new FormControl(null)

		})
	}
	onFileSelected(event) {
		const file: File = event.target.files[0];
		this.file = event.target.files[0];
		this.file = file;
		this.fileName = file.name;
		this.documentList = event.target.files;
		this.documentPath = event.target.value;
		//console.log(file.name)
		//console.log(this.documentPath)
		this.selectedFiles = event.target.files;
		//console.log(this.documentList.values)

		for (let fil of event.target.files) {
			this.files.push({
				name: fil.name,
				type: fil.type
			});


		}

		for (var i = 0; i < event.target.files.length; i++) {
			this.myFiles.push(event.target.files[i]);

		}
		if (file) {
			//this.File_name = file.name;
			this.totalFiles = this.myFiles.length;
			//console.log(this.documentList)
			//console.log(this.File_name[0])
			//console.log(i)

		}
	}

	upload(): void {

		this.progress = 0;
		this.patientDocuments = this.patientDForm.value;
		this.patientDocuments.patient = this.encounter.patient;
		this.patientDocuments.department = this.encounter.department;
		this.patientDocuments.serviceCenter = this.encounter.serviceCenter;
		this.patientDocuments.visit = this.encounter.visit;
		this.patientDocuments.uploadedBy = null;
		this.patientDocuments.documentUploadedDate = this.documentUploadedDate;
		//this.patientDocuments.documentNo = null;
		//this.patientDocuments.fileDescription = null;
		//this.patientDocuments.fileName = this.fileName;
		//this.patientDocuments.folderPath = this.folderPath;

		var formData = new FormData();

		formData.append('patientDocument', JSON.stringify(this.patientDocuments))
		formData.append('document', this.file);
		console.log(formData)

		console.log(this.patientDocuments)
		this.patientDocumentsService.uploadPatientDocumentWithFile(formData).subscribe(data => {
			//console.log(data);
			alert("Successfully inserted!!!!!")
		})

		/*this.patientDocumentsService.UploadPatientDocuments(this.patientDocuments).subscribe(data =>{
			console.log(patientDocuments)
			alert("file uploaded successfully")
		})*/
		//this.patientDocuments.filePath = this.

	}

	showFileName(data) {
		console.log(data);
		console.log(document.getElementById("fileId"));
		console.log(data.target.files)
	}
}


